import React, { useState } from "react";
import { SketchPicker } from "react-color";
import Style from "./Card.module.css";

function App() {
  const [backgr, setBrackgr] = useState({});
  const [opp, setOpp] = useState({});
  const [convHex, setConvHex] = useState('#fff');

  const colorHandler = (updatedColor) => {
    setBrackgr(updatedColor.rgb);
    setOpp(backgr.a);
    setConvHex(updatedColor.hex);
  };


 

  return (
    <div>
      <h1 className={Style.title}>Component Color Selector [React]</h1>
      <div className={Style.colorSel}>
        <SketchPicker color={backgr} onChange={colorHandler} />
      </div>
      <div style={{backgroundColor:convHex, opacity:opp, transition: 'ease all 700ms'}} className={Style.card}>
        <h1> &lt;Component/&gt; </h1>
      </div>
    </div>
  );
}

export default App;
